@font-face { font-family: Verdana; src: url("../fonts/verdana-italic.ttf") format("truetype"); font-weight: 400; font-style: normal; font-display: swap; }

@font-face { font-family: Verdana; src: url("../fonts/verdana-italic-bold.ttf") format("truetype"); font-weight: 700; font-style: normal; font-display: swap; }

@font-face { font-family: Verdana; src: url("../fonts/verdana-bold.ttf") format("truetype"); font-weight: 700; font-style: normal; font-display: swap; }

@font-face { font-family: Verdana; src: url("../fonts/verdana-regular.ttf") format("truetype"); font-weight: 400; font-style: normal; font-display: swap; }

body { position: relative; margin: 0 auto; padding: 0; line-height: 1.2; font-family: Verdana, Arial, Helvetica, sans-serif; overflow-x: hidden; }

img, video, audio { max-width: 100%; height: auto; }

.icon-facebook { width: 0.53em; height: 1em; fill: none; }

.text { margin: 0; color: black; font-family: Verdana; font-size: 14px; font-weight: 400; line-height: 20px; text-decoration: none; }

.text--font-lg { font-size: 16px; line-height: 20px; }

@media only screen and (min-width: 1280px) { .text { font-size: 18px; font-weight: 400; line-height: 26px; }
  .text--font-lg { font-size: 20px; line-height: 36px; } }

.header__wrapper { width: auto; margin: 0 auto; box-sizing: border-box; position: relative; padding: 15px 10px 120px; overflow-x: hidden; }

@media only screen and (min-width: 768px) { .header__wrapper { width: 768px; } }

@media only screen and (min-width: 1280px) { .header__wrapper { width: 1280px; } }

.header__wrapper::before { content: ""; position: absolute; width: 697px; height: 573px; background: url("../img/header@mb.png"); background-size: cover; z-index: -1; top: -150px; left: 30px; }

.header__container { display: flex; justify-content: space-between; align-items: center; margin-bottom: 235px; }

.header__logo img { width: 124px; height: 20px; }

.header__contacts { position: relative; margin: 0; padding: 14px 20px; list-style: none; }

.header__contacts::before { content: ""; position: absolute; z-index: -1; top: 0; right: -500px; bottom: 0; left: 0; background-color: rgba(255, 255, 255, 0.6); border-radius: 37px; }

.header__link { overflow: hidden; text-overflow: ellipsis; white-space: nowrap; color: black; font-family: Verdana; font-size: 12px; font-weight: 700; line-height: 14px; text-decoration: none; transition: all 0.3s ease; }

.header__link:hover { text-decoration: underline; }

@media only screen and (min-width: 768px) { .header__wrapper::before { content: ""; position: absolute; width: 697px; height: 443px; top: -126px; right: 0; left: initial; } }

@media only screen and (min-width: 1280px) { .header__wrapper { padding: 60px 16px 256px; overflow-x: initial; }
  .header__wrapper::before { content: ""; position: absolute; width: 1920px; height: 700px; background: url("../img/header@dt.jpg"); background-size: cover; top: 0; right: -300px; background-position: center; }
  .header__wrapper .text { padding-right: 690px; }
  .header__container { align-items: flex-start; margin-bottom: 206px; }
  .header__logo img { width: 305px; height: 48px; }
  .header__contacts { display: flex; }
  .header__contact:not(:last-of-type) { margin-right: 25px; }
  .header__link { font-size: 16px; line-height: 19.02px; } }

.catalog { background-color: #e6f0c9; background-image: linear-gradient(to top, #f8f8f2 0%, white 100%); }

.catalog__wrapper { width: auto; margin: 0 auto; box-sizing: border-box; padding: 0 10px 75px; }

@media only screen and (min-width: 768px) { .catalog__wrapper { width: 768px; } }

@media only screen and (min-width: 1280px) { .catalog__wrapper { width: 1280px; } }

.catalog__title { margin: 0 0 54px; color: black; font-family: Verdana; font-size: 22px; font-weight: 400; line-height: 26px; text-align: center; }

.catalog__title strong { font-family: Verdana; font-weight: 700; text-transform: uppercase; }

.catalog__cards { margin: 0; padding: 0; list-style: none; }

.catalog__card { position: relative; padding: 35px 20px; text-align: center; border-radius: 10px; border: 1px solid #95c11f; background-color: white; }

.catalog__card img { display: block; margin: 0 auto; }

.catalog__card--active { border: 1px solid #95c11f; box-shadow: 0 0 18px rgba(149, 193, 31, 0.3); }

.catalog__card--active .catalog__caption { margin-bottom: 25px; color: #95c11f; }

.catalog__card--active .text { margin-bottom: 20px; }

.catalog__card--flag { padding-right: 80px; padding-left: 80px; }

.catalog__card--flag::before { top: 35px; left: 25px; }

.catalog__card--ua::before { content: ""; position: absolute; width: 31px; height: 21px; background: url("../img/flag-ua.svg"); background-size: cover; }

.catalog__card--nl .catalog__caption { margin-bottom: 20px; }

.catalog__card--nl::before { content: ""; position: absolute; width: 31px; height: 21px; background: url("../img/flag-nl.svg"); background-size: cover; }

.catalog__card--plants .catalog__caption { margin-bottom: 20px; }

.catalog__card--minerals .catalog__caption { margin-bottom: 20px; }

.catalog__card--micro .catalog__caption { margin-bottom: 20px; }

.catalog__card--spare .catalog__caption { display: flex; margin-bottom: 20px; }

.catalog__card--spare .catalog__caption img { margin-left: 30px; }

.catalog__card:not(:last-of-type) { margin-bottom: 35px; }

.catalog__card-inner { margin-bottom: 35px; }

.catalog__cards-inner { margin: 0; padding: 0; list-style: none; }

.catalog__caption { margin: 0; color: black; font-family: Verdana; font-size: 16px; font-weight: 700; line-height: 20px; text-transform: uppercase; }

@media only screen and (min-width: 768px) { .catalog__cards { display: flex; flex-wrap: wrap; }
  .catalog__card { flex-grow: 1; flex-basis: 45%; margin-bottom: 26px !important; box-sizing: border-box; }
  .catalog__card:not(:last-of-type) { margin: 0; }
  .catalog__card:nth-of-type(2n+1) { margin-right: 26px; }
  .catalog__card:not(:nth-last-of-type(2)) { margin-bottom: 0; }
  .catalog__card-inner { flex-grow: 1; flex-basis: 45%; margin-bottom: 26px; }
  .catalog__cards-inner { display: flex; flex-direction: column; height: 100%; }
  .catalog__cards-inner .catalog__card { margin-bottom: 0 !important; }
  .catalog__cards-inner .catalog__card:nth-of-type(2n+1) { margin: 0 0 26px !important; }
  .catalog__cards-inner .catalog__card:not(:nth-last-of-type(2)) { margin-bottom: 0; } }

@media only screen and (min-width: 1280px) { .catalog__wrapper { padding: 0 16px 115px; }
  .catalog__title { margin-bottom: 51px; font-size: 32px; line-height: 36px; }
  .catalog__caption { font-size: 20px; line-height: 23.46px; }
  .catalog__card { padding: 30px 20px; }
  .catalog__card--flag { padding-right: 120px; padding-left: 120px; }
  .catalog__card--flag::before { top: 42px; left: 68px; }
  .catalog__card--spare .catalog__caption { justify-content: center; align-items: center; } }

.info { position: relative; overflow: hidden; }

.info::before { content: ""; position: absolute; width: 400px; height: 100%; background: url("../img/info@mb.jpg"); background-size: cover; z-index: -1; top: 0; right: 0; bottom: 0; left: 0; background-size: cover; }

.info__wrapper { width: auto; margin: 0 auto; box-sizing: border-box; padding: 75px 10px 85px; text-align: center; }

@media only screen and (min-width: 768px) { .info__wrapper { width: 768px; } }

@media only screen and (min-width: 1280px) { .info__wrapper { width: 1280px; } }

.info__title { margin: 17px 0 22px; color: #95c11f; font-family: Verdana; font-size: 22px; font-weight: 700; line-height: 26px; text-transform: uppercase; }

.info__banner { margin: 30px 0; padding: 30px 0; border-radius: 10px; background-color: rgba(255, 255, 255, 0.53); }

.info__link { display: block; margin: 34px 0 28px; color: red; font-family: Verdana; font-size: 16px; font-weight: 700; line-height: 23.47px; text-decoration: underline; }

.info__container .text:first-of-type { margin-bottom: 10px; }

@media only screen and (min-width: 768px) { .info::before { content: ""; position: absolute; width: 100%; height: 100%; background: url("../img/info@dt.jpg"); background-size: cover; } }

@media only screen and (min-width: 1280px) { .info__wrapper { padding: 115px 16px 156px; }
  .info__title { margin: 18px 0 26px; font-size: 32px; font-weight: 700; line-height: 36px; }
  .info__banner { width: 751px; margin: 36px auto 48px; padding: 44px 80px 38px; box-sizing: border-box; }
  .info__container { width: 602px; margin: 0 auto; }
  .info__container .text:first-of-type { margin-bottom: 24px; }
  .info__link { font-size: 18px; font-weight: 700; line-height: 23.46px; } }

.contacts { background-color: #f8f8f2; }

.contacts__wrapper { width: auto; margin: 0 auto; box-sizing: border-box; padding: 74px 10px 79px; text-align: center; }

@media only screen and (min-width: 768px) { .contacts__wrapper { width: 768px; } }

@media only screen and (min-width: 1280px) { .contacts__wrapper { width: 1280px; } }

.contacts__title { margin: 0 0 54px; color: black; font-family: Verdana; font-size: 22px; font-weight: 700; line-height: 26px; text-transform: uppercase; }

.contacts__cards { margin: 0; padding: 0; list-style: none; }

.contacts__card { padding: 35px 0; box-shadow: 0 6px 5px rgba(75, 75, 75, 0.2); border-radius: 10px; border: 1px solid #95c11f; background-color: white; }

.contacts__card:not(:last-of-type) { margin-bottom: 30px; }

.contacts__link { display: block; margin-bottom: 18px; color: black; font-family: Verdana; font-size: 16px; font-weight: 700; line-height: 22px; text-decoration: none; transition: all 0.3s ease; }

.contacts__link:hover { text-decoration: underline; }

@media only screen and (min-width: 768px) { .contacts__cards { display: flex; }
  .contacts__card { flex-grow: 1; flex-basis: 30%; }
  .contacts__card:not(:last-of-type) { margin: 0 27px 0 0; } }

@media only screen and (min-width: 1280px) { .contacts__wrapper { padding: 104px 16px 158px; }
  .contacts__title { font-size: 32px; font-weight: 700; line-height: 36px; }
  .contacts__card { padding: 55px 0; }
  .contacts__link { font-size: 18px; font-weight: 700; line-height: 23.46px; } }
